<template lang="html">
  <div class="hd-textarea">
    <label class="hd-textarea-label" :for="unique" v-text="label"></label>
    <textarea class="hd-textarea-core" v-model="model" :id="unique" :placeholder="placeholder" :maxlength="maxlength" :disabled="disabled" @change="onchange"></textarea>
    <div class="hd-textarea-record" v-text="wordRecord"></div>
  </div>
</template>

<script>
export default {
  name: 'hd-textarea',
  props: {
    unique: String,
    label: {
      type: String,
      required: true
    },
    value: {
      type: [String, Number],
      default: ''
    },
    placeholder: String,
    maxlength: [Number, String],
    disabled: Boolean
  },
  data () {
    return {
      model: this.value
    }
  },
  computed: {
    wordRecord () {
      return ('' + this.model).length + (this.maxlength ? '/' + this.maxlength : '')
    }
  },
  watch: {
    value (newValue) {
      this.model = newValue
    }
  },
  methods: {
    onchange () {
      this.$emit('change-value', this.model, this.unique)
    }
  }
}
</script>

<style lang="pcss">
@namespace hd {
  @b textarea {
    display: flex;
    flex-wrap: wrap;
    font-size: 32px;
    padding: 25px 20px 0;
    line-height: 1.2;
    border-bottom: 1px solid #d9d9d9;
    background-color: #fff;

    @e label {
      width: 160px;
      flex-shrink: 0;
      color: #666;
    }

    @e core {
      flex-grow: 1;
      border: none;
      outline: none;
      appearance: none;
      resize: none;
      font-size: inherit;
      color: inherit;
      line-height: 1.2;
      height: calc(38.4 * 3)px;
    }

    @e record {
      width: 100%;
      font-size: 24px;
      padding: 10px;
      color: #b2b2b2;
      text-align: right;
    }
  }
}
</style>



